@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{scroll-behavior:smooth}body{padding-bottom:9%;background:linear-gradient(#2e0031, black);text-align:center;font-size:62.5%}body h1,body h2,body h3,body h4,body h5,body h6,body p{color:white}body h1{font-size:5rem}body h2{font-size:4rem}body h3{font-size:3.5rem}body h4{font-size:3rem}body h5{font-size:2.5rem}body h6{font-size:2rem}body p{font-size:1.6rem}.app-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}nav{width:100%;border:1px solid blue;background:linear-gradient(to right, white, #005eb8, #005eb8);background-color:#005eb8;display:grid;grid-template-columns:4fr repeat(5, 1fr) 0.5fr;align-items:center;position:fixed;z-index:2}nav a{margin:1%;padding:0.5% 1%;color:white;font-family:"Permanent Marker", cursive;font-size:1.2rem;transition-duration:300ms}nav a:hover{text-shadow:white 1px 1px 5px;color:#052281}nav a:first-child{font-size:3rem;text-decoration:none;justify-self:flex-start;color:#fff;text-shadow:black -2px 1px 8px;padding:0.5%}nav a:first-child:hover{text-shadow:white 1px 1px 5px;color:#052281}nav a:last-child:hover{color:white;text-shadow:none;cursor:default}h1{position:absolute;z-index:1;width:100%;top:20%}.banner{background-image:url(/radio_page/static/media/alexander-popov-JhYnL-BiP18-unsplash.c077bcdb.jpg);height:50vh;padding-top:10%;background-position-y:center;background-size:cover;background-repeat:no-repeat;-webkit-filter:blur(3px);filter:blur(3px);display:flex;justify-content:center;align-items:center}.banner h1,.banner h2{color:white;text-shadow:0px 0px black, 0px 1px white, black 3px 3px}.social h3{padding-bottom:2%}.social .bubble_holder{width:60%;margin:0 auto;display:flex;justify-content:space-around}.social .bubble_holder i{background:linear-gradient(rgba(255,255,255,0.15), rgba(255,255,255,0.19), rgba(255,255,255,0.39), rgba(255,255,255,0.19), rgba(255,255,255,0.15));border-radius:180%;padding:3%}.social .bubble_holder .bubble_me{border:1px solid black;border-radius:50%;width:-webkit-max-content;width:-moz-max-content;width:max-content}#djs,#social,#info{padding-top:7%}.djs{width:80%;margin:0 10%;display:grid;grid-template-columns:1fr 1fr 1fr;grid-column-gap:3%;-webkit-column-gap:3%;column-gap:3%;grid-row-gap:2%;row-gap:2%}.djs img{width:100%;margin:0 auto}.player{border:1px solid white;position:fixed;z-index:1;width:100%;bottom:0;border:1px solid blue;background:linear-gradient(to left, white, #005eb8, #005eb8);background-color:#005eb8}.player .controls{padding:1%;width:60%;margin:0 auto;display:flex;justify-content:space-between}.player .controls i{padding:1%;border-radius:100%}#info div{width:80%;margin:0 auto}i{transition-duration:200ms;border:1px solid white;color:rgba(0,162,255,0.9)}i:hover{cursor:pointer;color:white;border:white 1px solid}.chat #message-container{background:white;color:black}

